<template>
  <div class="hello">
    <div class="promise-box">
      <div class="img-box"><img src="../assets/28.jpg"></div>
      <div class="write-into">
        <router-link to="/information"> <span class="write-into-span" bindtap="writeIntoClick">同意并填写个人信息及签字</span></router-link>
        <span class="write-into-span2">您的个人信息我们会予以保密</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .promise-box{
    width: 100%;
    height: 100%;
    position: absolute;
    background: #fff;
  }
  .img-box{
    position: relative;
    margin: 60px 0;
    width: 90%;
    left: 5%;
  }
  .img-box img{
    position: relative;
    width: 100%;
  }
  .write-into{
    position: absolute;
    width: 100%;
    line-height: 40px;
    border-radius: 20px;
    bottom: 30px;
    text-align: center;
  }
  .write-into-span{
    position: relative;
    display: block;
    width: 80%;
    left: 10%;
    height: 40px;
    line-height: 40px;
    border-radius: 20px;
    text-align: center;
    font-size: 14px;
    color: #fff;
    background: linear-gradient(120deg, #43A1FF, #785EFF);
  }
  .write-into-span2{
    position: relative;
    display: block;
    width: 80%;
    left: 10%;
    font-size: 12px;
    color: #999;
  }
</style>



