<template>
    <div class="information">
      <!--style="{{'display:' + inforshow + ';'}}"-->
      <div class="information" :style="[information?show:hidden]">
        <div class="information-inbox">
          <span>姓名：</span>
          <input  class="information-form" auto-focus="true" type="text" v-model="nameInput" bindinput="nameInput" confirm-type="next"  placeholder="请输入姓名" maxlength="6"/>
        </div>
        <div class="information-inbox">
          <span>小区：</span>
          <input  class="information-form" type="text" v-model="areasInput"  bindinput="areasInput" confirm-type="next" placeholder="例：滨河社区" maxlength="20"/>
        </div>
        <div class="information-inbox">
          <span>幢：</span>
          <input  class="information-form" type="text" v-model="TungInput"  bindinput="TungInput" confirm-type="next" placeholder="例：13" maxlength="3"/>
        </div>
        <div class="information-inbox">
          <span>单元：</span>
          <input  class="information-form" type="text" v-model="unitInput"  bindinput="unitInput" confirm-type="next" placeholder="例：2" maxlength="1"/>
        </div>
        <div class="information-inbox">
          <span>室：</span>
          <input  class="information-form" type="text" v-model="roomInput"  bindinput="roomInput"  confirm-type="next" adjust-position="true" placeholder="例：302" maxlength="6"/>
        </div>

        <div class='content'>
          <span class="content-title">请在签字框中签字：</span>
          <canvas class="firstCanvas" width="300" height="300" id="canvas" @touchmove.prevent>Canvas画板</canvas>
          <!--<canvas class='firstCanvas'-->
                  <!--canvas-id="canvas"-->
                  <!--bindtouchmove='move'-->
                  <!--bindtouchstart='start'-->
                  <!--bindtouchend='end'-->
                  <!--bindtouchcancel='cancel'-->
                  <!--bindlongtap='tap'-->
                  <!--disable-scroll='true'-->
                  <!--binderror='error'>-->
          <!--</canvas>-->
          <img v-bind:src="url" alt="" id="ifmimg">
          <button bindtap='clearClick' v-on:click="clear">清除签字重写</button>
          <!--<button bindtap='saveClick' >保存签字 </button>-->
        </div>

        <div class="information-form-input" v-on:click="save"><span>生成承诺书</span></div>
        <div class="back-input" bindtap="backClick" ><span>取消</span></div>
      </div>

      <div class="promiseed-box" :style="[promiseed?show:hidden]">
        <canvas class="promiseed-canvas" id="proCanvas"/>
        <div class="confirm" v-on:click="confirmClick"><span>确认并提交</span></div>
      </div>

    </div>
</template>

<script>
  var draw;
  var preHandler = function(e){e.preventDefault();};
  class Draw {
    constructor(el) {
      this.el = el;
      this.canvas = document.getElementById(this.el);
      this.cxt = this.canvas.getContext('2d');
      this.stage_info = canvas.getBoundingClientRect();
      this.path = {
        beginX: 0,
        beginY: 0,
        endX: 0,
        endY: 0
      }
    }
    init(btn) {
      var that = this;
      this.canvas.addEventListener('touchstart', function(event) {
        document.addEventListener('touchstart', preHandler, false);
        that.drawBegin(event)
      });
      this.canvas.addEventListener('touchend', function(event) {
        document.addEventListener('touchend', preHandler, false);
        that.drawEnd()
      });
      this.clear(btn)
    }

    drawBegin(e) {
      var that = this;
      window.getSelection() ? window.getSelection().removeAllRanges() : document.selection.empty();
      this.cxt.strokeStyle = "#000";
      this.cxt.beginPath();
      this.cxt.moveTo(
        e.changedTouches[0].pageX - this.stage_info.left,
        e.changedTouches[0].pageY - this.stage_info.top
      );
      this.path.beginX = e.changedTouches[0].pageX - this.stage_info.left;
      this.path.beginY = e.changedTouches[0].pageY - this.stage_info.top;
      canvas.addEventListener("touchmove",function(){
        that.drawing(event)
      })
    }

    drawing(e) {
      this.cxt.lineTo(
        e.changedTouches[0].pageX - this.stage_info.left,
        e.changedTouches[0].pageY - this.stage_info.top
      );

      this.path.endX = e.changedTouches[0].pageX - this.stage_info.left;
      this.path.endY = e.changedTouches[0].pageY - this.stage_info.top;

      this.cxt.stroke()
    }

    drawEnd() {
      document.removeEventListener('touchstart', preHandler, false);
      document.removeEventListener('touchend', preHandler, false);
      document.removeEventListener('touchmove', preHandler, false);
      //canvas.ontouchmove = canvas.ontouchend = null
    }

    clear(btn) {
      this.cxt.clearRect(0, 0, 300, 600)
    }

    save(){
      // this.signImage = canvas.toDataURL("image/png");
      console.log(canvas.toDataURL("image/png"));
      return canvas.toDataURL("image/png");
    }

  }
    export default {
        name: 'Information',
        data() {
            return {
              val:true,
              url:"",
              nameInput: '某某某',
              areasInput: '春波小区',
              TungInput: '10',
              unitInput: '5',
              roomInput: '302',
              screenHight:'',
              screenWidth:'',
              promiseshow1:true,
              information:true,
              promiseed: false,
              show: {'display':'block'},
              hidden: {'display':'none'},
              context: null,
              signImage:'',
            }
        },
      methods: {
        clear:function(){
          draw.clear();
        },
        save:function(){
          var that = this;
          this.information = false;
          this.promiseed = true;
          var data=draw.save();
          this.url = data;
          console.log(data);

          var context = this.context;
          var text0 = '居民垃圾分类承诺书';//这是标题文本
          var text1 = '        本人' + this.nameInput + '承诺居住在' + this.areasInput + this.TungInput + '幢' + this.unitInput + '单元' + this.roomInput + '室期间，遵守以下事：';//这是要绘制的文本1
          var text2 = '        1、自觉做好垃圾分类，并正确投放。';
          var text2_1 = '        2、严格按照定时定点要求，在早上7:00-9:30，晚上6:00-8:30两个时间段投放垃圾。';
          var text2_2 = '        3、带动家人、朋友、邻居等共同参与垃圾分类。';
          var text3 = '承诺人：' + this.nameInput;//承诺人
          var myDate = new Date();
          var text4 = myDate.getFullYear() + '年' + myDate.getMonth() + '月' + myDate.getDate() + '日';//时间
          var texth = 1;//文字总行数
          var textheight = 0;//文字总高度
          context.font = 'normal bold 20px sans-serif';

          var teli = function() {
            var chr0 =text0.split("");
            var temp0 = "";
            var row0 = [];
            for (var a = 0; a < chr0.length; a++) {
              // console.log(temp0);
              if (context.measureText(temp0).width < 180) {
                temp0 += chr0[a];
              }
              else {
                a--; //这里添加了a-- 是为了防止字符丢失，效果图中有对比
                row0.push(temp0);
                temp0 = "";
              }
            }
            row0.push(temp0);
            // console.log((that.$parent.globalData.canvasw-180)/2);
            context.setFillStyle='#333';
            for (var b = 0; b < row0.length; b++) {
              context.fillText(row0[b], (that.screenWidth - context.measureText(temp0).width)/2, 60 + b * 30);
              context.font = '16px sans-serif';
            }
          };

          var tongyong = function(a,h,right) {
            var chr =a.split("");//这个方法是将一个字符串分割成字符串数组
            var temp = "";
            var row = [];
            for (var a = 0; a < chr.length; a++) {
              // console.log(temp);
              if (context.measureText(temp).width < that.screenWidth*0.8) {
                temp += chr[a];
              }
              else {
                a--; //这里添加了a-- 是为了防止字符丢失，效果图中有对比
                row.push(temp);
                temp = "";
              }
            }
            row.push(temp);
            for (var b = 0; b < row.length; b++) {
              if (right==true){
                context.fillText(row[b], that.screenWidth*0.9-context.measureText(temp).width, texth*30+50+b*30+h);
              }
              else{
                context.fillText(row[b], that.screenWidth*0.1, texth*30+70+b*30+h,);
              }
            }
            texth += row.length;
            textheight+=(row.length*30);
          };

          setTimeout(()=>{
            teli();
            tongyong(text1,0,false);
            tongyong(text2,0,false);
            tongyong(text2_1,0,false);
            tongyong(text2_2,0,false);
            tongyong(text3,30,true);
            tongyong(text4,30,true);
            var img = document.getElementById('ifmimg');
            context.drawImage(img, that.screenWidth*0.3, textheight, 120, 120);
          }, 1000);
        },
        mutate(word) {
          this.$emit("input", word);
        },
        confirmClick:function () {
          this.signImage = proCanvas.toDataURL("image/png");
          console.log(this.signImage);
        }
      },
      mounted(){
        this.screenHight = document.documentElement.clientHeight;
        this.screenWidth = document.documentElement.clientWidth;
        var canvas = document.getElementById("proCanvas");
        canvas.width = document.documentElement.clientWidth;
        canvas.height = document.documentElement.clientHeight;
        this.context = canvas.getContext('2d');
        draw=new Draw('canvas');
        draw.init();
        draw.init();
      },


    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{margin: 0;padding: 0}
  .information{
    position: absolute;
    top: 20px;
    width: 90%;
    height: 100%;
    box-sizing: border-box;
    left: 5%;
    background: #fff;
  }
  .information-inbox{
    position: relative;
    width: 100%;
    height: 30px;
    margin-bottom: 20px;
    font-size: 14px;
    /*background: red;*/
    border-bottom: 1px solid #f0f0f0;
  }
  .information-inbox span{
    /*width: 20%;*/
    float: left;
  }
  .information-inbox input{
    width: 80%;
    display: inline-block;
    border: 0;
    outline: none;
  }

  .information-form-input{
    position: relative;
    /*bottom: -60px;*/
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .information-form-input span{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 40px;
    border-radius: 20px;
    background: linear-gradient(120deg, #43A1FF, #785EFF);
    color: #fff;
    font-size: 16px;
  }
  .back-input{
    position: relative;
    /*bottom: -60px;*/
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
  }
  .back-input span{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 26px;
    line-height: 26px;
    border-radius: 13px;
    /*background: linear-gradient(120deg, #43A1FF, #785EFF);*/
    background: #43A1FF;
    color: #fff;
    font-size: 12px;
  }
  .content {
    position: relative;
    width: 100%;
    /*height: 500px;*/
    /*background-color: red;*/
    /*display: none;*/
  }
  .firstCanvas {
    background-color: #f0f0f0;
    /*width: 300px;*/
    /*height: 300px;*/
  }
  .content button{
    position: relative;
    display: inline-block;
    width: 40%;
    left: 30%;
    margin: 10px 0 20px;
    height: 30px;
    line-height: 30px;
    border-radius: 15px;
    text-align: center;
    font-size: 14px;
    color: #785EFF;
    border: 1px solid #785EFF;
    /*background: linear-gradient(120deg, #43A1FF, #785EFF);*/
  }
  .content image {
    width: 100%;
    height: 200px;
    background-color: #fff;
  }
  .promiseed-box{
    position: absolute;
    width: 100%;
    height: 100%;
    background: #fff;
  }
  .promiseed-canvas{
    position: absolute;
    top: 0;
    width: 100%;
    height: 90%;
    border: 0;
    background: #f0f0f0;
  }
  .confirm{
    position: absolute;
    z-index: 5;
    width: 80%;
    left: 10%;
    height: 40px;
    line-height: 40px;
    border-radius: 20px;
    text-align: center;
    bottom: 20px;
    background: linear-gradient(120deg, #43A1FF, #785EFF);
    color: #fff;
    font-size: 16px;
  }
</style>



